export default {
  data() {
    return {
      activeName: "first",
      ruleForm: {
        count_range: "",
        coins_range: "",
        leave_rate: "",
        system_bet_min: "",
        system_last_bet_rate: "",
        system_bet_area: "",
        bet_area_float: "",
        vip_seat_rate: ""
      },
      rules: {
        count_range: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        coins_range: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        leave_rate: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        system_bet_min: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        system_last_bet_rate: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        system_bet_area: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        bet_area_float: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ],
        vip_seat_rate: [
          { required: true, message: "不可以为空", trigger: "blur" }
        ]
      },
      //当前游戏房间的所有数据
      currentlist: {},
      //匹配当前游戏的条件
      namelist: [],
      id: 0,
      keys: "",
      loading: false
    };
  },

  async created() {
    let { data } = await this.$http.HallFunConfig.Getbjl_robot_control({
      key: "bjl_robot_control.lua"
    });
    // console.log(data);
    this.id = data.data[0].id;
    this.keys = data.data[0].sys_key;
    let res = JSON.parse(data.data[0].sys_val);
    // console.log(res);
    this.currentlist = res.bjl_normal.robot_type_list;
    this.namelist = Object.keys(this.currentlist);
    this.activeName = this.namelist[0];
    this.ruleForm = this.currentlist[this.namelist[0]];
    console.log(this.ruleForm, this.currentlist, this.namelist);
  },

  methods: {
    handleClick(tab) {
      this.namelist.forEach(item => {
        if (item === tab.name) {
          this.ruleForm = this.currentlist[item];
        }
      });
    },

    submitForm(formName, type) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          console.log(this.ruleForm, this.currentlist);
          if (type === 1) {
            //put
            let { data } = await this.$http.HallFunConfig.Putbjl_robot_control({
              keys: this.keys,
              values: JSON.stringify({
                bjl_normal: { robot_type_list: this.currentlist }
              }),
              id: this.id
            });
            // console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.$message({
                type: "success",
                message: "保存成功!"
              });
            } else {
              this.$message({
                type: "warning",
                message: "保存失败!"
              });
            }
          } else if (type === 2) {
            //post
            this.loading = true;

            let { data } = await this.$http.HallFunConfig.Postbjl_robot_control(
              {
                keys: this.keys,
                values: JSON.stringify({
                  bjl_normal: { robot_type_list: this.currentlist }
                }),
                id: this.id
              }
            );
            // console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.loading = false;
              this.$message({
                type: "success",
                message: "发送服务器配置成功!"
              });
            } else {
              this.loading = false;
              this.$message({
                type: "warning",
                message: "发送服务器配置失败!"
              });
            }
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};



